import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


// Layouts
import { LayoutAdminComponent } from './shared/components/layout-admin/layout-admin.component';
// import { LayoutSimpleComponent } from './shared/layouts/simple/simple.component';


import {GestionclasesComponent} from './gestionclases/gestionclases.component';

import {ProgramarClaseComponent} from './programar-clase/programar-clase.component';

import {UsuariosComponent} from './usuarios/usuarios.component';

import {AsignarComponent} from './asignar/asignar.component';


import {EjerciciosComponent} from './ejercicios/ejercicios.component';

import {RutinaComponent} from './rutina/rutina.component';

import {ProfesoresComponent} from './profesores/profesores.component';
import {MaquinasComponent} from './maquinas/maquinas.component';
import {NovedadesComponent} from './novedades/novedades.component';


const routes: Routes = [
  {
    path: '',
    redirectTo: 'dashboard',
    pathMatch: 'full',
  },
  {
    path: '',
    component: LayoutAdminComponent,
    children: [
      {
        path: 'dashboard',
        loadChildren: './dashboard/dashboard.module#DashboardModule'
      },
      {
        path: 'plong',
        loadChildren: './plong/plong.module#PlongModule'
      },

      {
        path: 'servicios',
        component: GestionclasesComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },

            {
        path: 'calendario',
        component: ProgramarClaseComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },


            {
        path: 'usuarios',
        component: UsuariosComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },

              {
        path: 'asignar',
        component: AsignarComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },


            {
        path: 'citas',
        component: EjerciciosComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },


            {
        path: 'cupones',
        component: RutinaComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },

                  {
        path: 'profesores',
        component: ProfesoresComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },

                  {
        path: 'maquinas',
        component: MaquinasComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },

                        {
        path: 'novedades',
        component: NovedadesComponent
       // loadChildren: './plong/plong.module#PlongModule'
      },




      // {
      //   path: 'offerassets',
      //   loadChildren: './offerassets/offerassets.module#OfferassetsModule'
      // },
      // {
      //   path: 'offersetups',
      //   loadChildren: './offersetups/offersetups.module#OffersetupsModule'
      // },
      // {
      //   path: 'offertemplates',
      //   loadChildren: './offertemplates/offertemplates.module#OffertemplatesModule'
      // },
      // {
      //   path: 'offerteasers',
      //   loadChildren: './offerteasers/offerteasers.module#OfferteasersModule'
      // },
    ]
  },
  // {
  //   path: '',
  //   component: LayoutSimpleComponent,
  //   children: [
  //     {
  //       path: 'error',
  //       loadChildren: './error/error.module#ErrorModule'
  //     },
  //   ]
  // },
  {path: '**', redirectTo: 'error'}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {
}



