import { Component, OnInit } from '@angular/core';
import { EndpointsService } from '../endpoints.service';


import * as $ from 'jquery';

window["$"] = $;
window["jQuery"] = $;



@Component({
  selector: 'tox-programar-clase',
  templateUrl: './programar-clase.component.html',
  styleUrls: ['./programar-clase.component.scss']
})


export class ProgramarClaseComponent implements OnInit {
permiteReserva : boolean;
  selected = [];

profesores = [];


 dateObj = new Date();

minDatse = {year: (this.dateObj.getUTCFullYear()), month: (this.dateObj.getUTCMonth() + 1), day: (this.dateObj.getUTCDate())};

minDats2 = (this.dateObj.getUTCFullYear())+'-'+(this.dateObj.getUTCMonth() + 1)+'-'+(this.dateObj.getUTCDate());


      loading = false;

//caleninit

    calendarOptions:Object = {
    //  height: 'parent',
      fixedWeekCount : false,
      defaultDate: this.minDats2,
      editable: false,
      eventLimit: true, // allow "more" link when too many events
      textColor: '#fff',
      events: [
      ]
    };

    onCalendarInit(initialized: boolean) {
      console.log('Calendar initialized');
    }

//calenend
public programacion ={};
clases = [];
clasesActivas=[];







  constructor(public endpointsService: EndpointsService) { 

  }

  ngOnInit() {
  //this.cargaClases();

  this.cargaEventos();


  }


  verificarReserva(f) {
   (this.clases.find(x => x.idClase == f)['reserva']) == 1 ? this.permiteReserva = true : this.permiteReserva = false;


  }

    cargaClases(){

        this.endpointsService.getClasesProfesores()
    .then((data:any) => {    
      console.log(data[0]);
      this.clases = data[0];
        this.profesores = data[1];
     // this.originalData = data;
     });


  }

  cargaEventos(){

        this.endpointsService.getCentroServicios({idCentro:localStorage.getItem('idCentroBY')})
    .then((data:any) => {    
      //console.log(data);
      //this.serviciosCentro = data.servicios;
    
     });


    this.endpointsService.citasCentroC2({idCentro:localStorage.getItem('idCentroBY')})
    .then((data:any) => {    
      //console.log(data);

      	//this.clasesActivas=data;
        let newEvents = data.map(function(evento){
      	return  {id:evento.idCita,
      			 title: evento.nombreCliente,
          		 start: evento.horaInicio,
          		 color: evento.estado == 1 ? '#DAA520' : '#6DC066',
          		  textColor: '#fff'
          		 }
         });
         	$('#myCalendar').fullCalendar('removeEvents');
             this.calendarOptions['events'] = newEvents;
           //  console.log(this.calendarOptions.events);
   			$('#myCalendar').fullCalendar('renderEvents', newEvents, true);

     });

  

  }

	pad2(number) {
	    return (number < 10 ? '0' : '') + number;
	}



  onSelect({ selected }) {
    console.log(this.selected[0]);

  }


 
      cancelarClase(id) {

       this.loading = true;

      this.endpointsService.cambiarEstadoClase(id,3)
    .then((data:any) => {    
      console.log(data);
       this.loading = false;
         this.cargaEventos();
     },  msg => { // Error
        console.log(msg);
        this.loading = false;
        });


  }





      completarClase(id) {

       this.loading = true;

      this.endpointsService.cambiarEstadoClase(id,2)
    .then((data:any) => {    
      console.log(data);
       this.loading = false;

         this.cargaEventos();
     },  msg => { // Error
        console.log(msg);
        this.loading = false;
        });


  }




    guardarProgramacion(){

   
    this.programacion['fechaCompleta']= this.programacion['fecha']['year']+'-'+ 
    								 this.pad2(this.programacion['fecha']['month'])+'-'+
    								 this.pad2(this.programacion['fecha']['day'])+' '+
    								 this.pad2(this.programacion['tiempo']['hour'])+':'+
    								 this.pad2(this.programacion['tiempo']['minute'])+':00';
     console.log(this.programacion);		 

        this.endpointsService.guardarProgramacion(this.programacion)
    .then((data:any) => {    
      //console.log(data);
      alert('Guardado correctamente');
      this.cargaEventos();
      this.programacion={};
      //this.clases = data;
     // this.originalData = data;
     }, err => {   alert('Ha ocurrido un error')});


  }






}



