import { Component, OnInit } from '@angular/core';
import { EndpointsService } from '../endpoints.service';


@Component({
  selector: 'tox-rutina',
  templateUrl: './rutina.component.html',
  styleUrls: ['./rutina.component.scss']
})
export class RutinaComponent implements OnInit {



rows = [];
selected = [];
  editar = false;
loadingI=false;
selectedHorario = [];
diasRutina = [];
diasRutinaFixed = [];
public keyFixed = [];
keysRutina = [];
ejerciciosSelect = [];


agregarEjercicio ={}; 
    usuarioSeleccionado = {};
  nuevoUsuario ={}; 
   rowsHorarios = [];


     public loading = false;
          public loading2 = false;
public isCollapsed = false;
  constructor(public endpointsService: EndpointsService) { }



  ngOnInit() {

  this.getCuponesCentro();
  //  this.cargaEjercicios();
  }



    getCuponesCentro(){

let dataE = {idCentro:localStorage.getItem('idCentroBY')};
        this.endpointsService.getCuponesCentro(dataE)
    .then((data:any) => {    
      this.rows = data.cupones;
     });


  }
    cargaEjercicios(){

        this.endpointsService.getEjercicios()
    .then((data:any) => {    
      //console.log(data);
      this.ejerciciosSelect = data;
    
     });


  }



getTitulo(d){
  
   if(d == 1) { return 'Activados'}; 
   if(d == 2) { return 'Usados'};
   if(d == 3) { return 'Expirados'};

}

      getClienteCupones(id){
this.loadingI=true;
        this.endpointsService.getClienteCupones({idCupon:id})
    .then((data:any) => {    
      console.log(data);


        this.diasRutina = Object.values(data);
        this.keysRutina = Object.keys(data);
        this.loadingI=false;


    
     });


  }



  onSelect({ selected }) {
 // this.loadingI=true;
  this.keyFixed = []; 
  this.diasRutinaFixed = [];
 
    this.usuarioSeleccionado = JSON.parse(JSON.stringify(this.selected[0]));

    let  dateObj = new Date(this.usuarioSeleccionado['fechaExpira']);
    this.usuarioSeleccionado['soloFecha'] =  {year: (dateObj.getUTCFullYear()), month: (dateObj.getUTCMonth() + 1), day: (dateObj.getUTCDate())};

  console.log(this.usuarioSeleccionado['idCupon']);
  this.getClienteCupones(this.usuarioSeleccionado['idCupon']);
  }





  guardarEdicion(){
 this.loading = true;

 console.log(this.usuarioSeleccionado);


   
    this.usuarioSeleccionado['fechaExpira']= this.usuarioSeleccionado['soloFecha']['year']+'-'+ 
                     this.pad2(this.usuarioSeleccionado['soloFecha']['month'])+'-'+
                     this.pad2(this.usuarioSeleccionado['soloFecha']['day'])+' '+'00:00'+':00';

 console.log(this.usuarioSeleccionado);
      this.endpointsService.editarCupon(this.usuarioSeleccionado)
    .then((data:any) => {    
      console.log(data);
       this.loading = false;
      this.getCuponesCentro();
       this.keysRutina=[];
       alert('Editado correctamente');
       this.editar = false;

     },  msg => { // Error
        console.log(msg);
       // this.loading = false;
        });
        
}

	pad2(number) {
	    return (number < 10 ? '0' : '') + number;
	}




  nuevoCupon(){

 this.loading = true;


 
this.nuevoUsuario['idCentro'] =1;
   this.nuevoUsuario['fechaExpira'] = this.nuevoUsuario['soloFecha']['year']+'-'+ 
                     this.pad2(this.nuevoUsuario['soloFecha']['month'])+'-'+
                     this.pad2(this.nuevoUsuario['soloFecha']['day'])
                     '00:00:00';
console.log(this.nuevoUsuario);

      this.endpointsService.nuevoCupon(this.nuevoUsuario)
    .then((data:any) => {    
      console.log(data);
       this.loading = false;
     this.getCuponesCentro();
       alert('Guardado correctamente');
       this.editar = false;
       this.nuevoUsuario={};

     },  msg => { // Error
        console.log(msg);
        this.loading = false;
        });
}


  getEjerciciosRutina(){
  

  this.endpointsService.getEjerciciosRutina({idRutina:this.usuarioSeleccionado['idRutina']})
    .then((data:any) => {    

    //console.log(Object.keys(data));
    //console.log(this.usuarioSeleccionado.diasXSemana);


    if(Object.keys(data).length !== this.usuarioSeleccionado['diasXSemana']){


    		for (var i = 0; i < this.usuarioSeleccionado['diasXSemana']; i++) {
   				this.keyFixed.push(''+(i+1));
			}

     		this.keysRutina = this.keyFixed;  //dias en view set



			this.keyFixed.forEach((item, index) => {
			//console.log(item);
			//console.log(index);
			//console.log(Object.values(data)[index]);
			//console.log(Object.values(data)[item-1][0].diaNumero);

     		if( (Object.values(data)[item-1])&&(item == Object.values(data)[item-1][0].diaNumero) ){

     		this.diasRutinaFixed[item-1]=Object.values(data)[item-1];
     		}
     		else{
     		
     		if(!this.diasRutinaFixed[item-1]){
     	//	console.log(item-1);
     		this.diasRutinaFixed[item-1]=[];
     		}

     		if(Object.values(data)[item-1]){

     		this.diasRutinaFixed[(Object.values(data)[item-1][0].diaNumero) - 1]=
     		Object.values(data)[item-1];
     	//	console.log(Object.values(data)[item-1][0].diaNumero);
     	//	console.log(Object.values(data)[item-1]);

     		}

     		}


			});

			this.diasRutina =this.diasRutinaFixed;
      this.loadingI=false;


    }	
    else{
    		this.diasRutina = Object.values(data);
     		this.keysRutina = Object.keys(data);
        this.loadingI=false;
    }
     //	console.log(this.diasRutina);
       // console.log(this.keysRutina);
     // this.rowsHorarios = data;

     });


  }





			  borrarEjercicioRutina(id){
			   this.loading2 = true;
			 let dataER = {idRutinaActividad:id};

			      this.endpointsService.borrarEjercicioRutina(dataER)
			    .then((data:any) => {    
			      console.log(data);
			       this.loading2 = false;
			       this.getEjerciciosRutina();
			       alert('Borrado correctamente');
			       //this.editar = false;
			       this.agregarEjercicio={};

			     },  msg => { // Error
			        console.log(msg);
			        this.loading2 = false;
			        });
			        
			}



			  agregarEjercicioRutina(dia,idEjercicio){
			   this.loading2 = true;
			 let dataER = {diaNumero:dia, idActividad:idEjercicio, idRutina:this.usuarioSeleccionado['idRutina']};

			      this.endpointsService.agregarEjercicioRutina(dataER)
			    .then((data:any) => {    
			      console.log(data);
			       this.loading2 = false;
			       this.getEjerciciosRutina();
			       alert('Guardado correctamente');
			       //this.editar = false;
			       this.agregarEjercicio={};

			     },  msg => { // Error
			        console.log(msg);
			        this.loading2 = false;
			        });
			        
			}




  onSelectHorario({ selectedHorario }) {
    console.log(this.selectedHorario[0]); 
   // this.rows = this.originalData;
   // this.test = JSON.parse(JSON.stringify(this.selected[0]));
   // this.claseSeleccionada = JSON.parse(JSON.stringify(this.selected[0]));
   // this.cargaUsuarios(this.selectedHorario[0].idReservaClase);
   
  }


}



