// routing for -----

@Component({
  selector: 'tox-sidebar',
  templateUrl: './sidebar.component.html',
})
export class SidebarComponent implements OnInit {

  solicitudesRegistro : number;
   solicitudesRutina : number;
   centroSeleccionado = '';
centrosUsuario =[];
  public menuItems: any[];

  constructor( public endpointsService: EndpointsService) {
  }



   ngOnInit() {
 this.menuItems = ROUTES.filter(menuItem => menuItem);
 // this.getSolicitudesEspera();
 this.getCentrosUsuario();
  }


cambiarCentro(idCentro, nombre){
  
  this.centroSeleccionado = nombre;
    localStorage.setItem( 'idCentroBY', idCentro );
      window.location.reload();
    console.log(localStorage.getItem( 'idCentroBY'));
}


    getCentrosUsuario(){
    let datE = {idUsuario:1};
        this.endpointsService.getCentrosUsuario(datE)
    .then((data:any) => {    
      this.centrosUsuario = data;
      let nombre = '';
       data.forEach((item)=>{

        if(item.idCentro == localStorage.getItem( 'idCentroBY')){
        nombre= item.nombre;


        }
        
      });

      this.centroSeleccionado = nombre;
    console.log(this.centrosUsuario );
     
     });


  }


    getSolicitudesEspera(){

        this.endpointsService.getSolicitudesEspera()
    .then((data:any) => {    
      this.solicitudesRutina = (data[0][0]['count(idUsuario)']);
      this.solicitudesRegistro = (data[1][0]['count(idUsuario)']);
    console.log(this.solicitudesRegistro);
     
     });


  }


}



